import axios from "axios"
import { Message } from 'element-ui'
import Cookies from 'js-cookie'
import store from '@/store'
export  const request =(options)=> {
    return new Promise((resolve, reject) => {
      // create an axios instance
        let baseSrc = process.env.VUE_APP_API_BASE_URL
        if (options.url.includes('/tts')) { 
          baseSrc = process.env.VUE_APP_API_VOICE_URL
        }else if (options.url.includes('/chat/completions')) {
          baseSrc = process.env.VUE_APP_API_CHAT_URL
        }
        const service = axios.create({
            baseURL:baseSrc,
            timeout: 1000000 
        })
        service.interceptors.request.use(
            (config) => {
                let token = store.state.userInfo.localId
                if (token) {
                    config.headers['token'] = encodeURIComponent(token)
                    // config.headers['Authorization'] = 'Bearer f-ee88d5b2b002da463c944e41c8936059'
                    // config.headers['Authorization'] = 'Bearer f-ee88d5b2b002da463c944e41c8936059'
                }
                return config
            },
            (error) => {
                console.log(error)
            }
        )
        service.interceptors.response.use(
            (response) => {
            if (response.data.code == 401) {
              Cookies.remove('token')
              store.commit('SET_LOGIN',false)
              store.commit('SET_USER', {})
              setTimeout(() => { 
                store.commit('OPEN_LOGIN',true)
              },800)
            } else if (response.data.code !== 200 && response.data.status!==200 && !response.data.model) {
              Message.error(response.data.msg || response.data.message || '出现了未知的错误，请联系管理员')
            }
            return response.data
            }, (error) => {
                console.log(error)
                let { message } = error;
                let msg = '系统出现了未知的错误，请联系管理员'
                if (message.includes("timeout")) {
                  msg = "系统接口请求超时"
                }
                Message.error(msg)
                if(error.request.responseType == 'blob'){
                    return Promise.reject(error)
                }else{
                    return Promise.reject(error)
                }
            }
        )
        // 请求处理
        service(options)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
    })
}
export default request


