<template>
  <div id="app">
    <Header></Header>
     <keep-alive> <router-view v-if="$route.meta.keepAlive" /></keep-alive>
     <router-view v-if="!$route.meta.keepAlive" />
    <!-- <Footer v-if="isShow&&!isPdfBot"></Footer> -->
    <!-- <GoTop v-if="isShow&&!isPdfBot"></GoTop> -->
  </div>
</template>

<script>
export default {
  name: 'App',
   data(){
        return{
            isShow:false,
            isPdfBot:false
        }
    },
    updated(){
      this.isPdfBot = ['/baseinfo/userControl','/baseinfo/userWorks','/baseInfo/userControl','/baseInfo/userWorks','/file_chat','/video_class','/user/notice','/api/docs','/mobile/index'].includes(this.$route.path)
      this.isShow = !this.$route.path.includes('/history')
    },
}
</script>

<style>
#app{
  background: #f2f3f5;
}
.custom-popover{
  min-width: 50px;
  padding:0;
}
</style>



