import Vue from 'vue'
import Vuex from 'vuex'
import { getCredit } from '@/api/api.js'
Vue.use(Vuex)

const state = {
  sidebarIndex: 0,
  isLogin: false,
  openLogin: false,
  userInfo: {},
  credit: 0
}
const mutations = {
  SET_SIDEBAR: (state, index) => {
    state.sidebarIndex = index
  },
  SET_LOGIN: (state, bool) => {
    state.isLogin = bool
  },
  OPEN_LOGIN: (state, bool) => {
    state.openLogin = bool
  },
  SET_USER: (state, info) => {
    state.userInfo = info
  }
  ,
  SET_CREDIT: (state, info) => {
    state.credit = info
  }
}
const actions = {
  setCredit ({ commit  }) {
    return new Promise((resolve, reject) => {
      getCredit().then(res=>{
        if(res.code==200){
          commit('SET_CREDIT', res.data.credit)
          resolve(res)
        }
      }).catch(err=>{
        reject(err)
      })
    })
  },
}
export default new Vuex.Store({
  state,
  mutations,
  actions
})


