// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import ViewUI from 'view-design'
import 'view-design/dist/styles/iview.css'
import ElementUI from 'element-ui';
import Cookies from 'js-cookie'
import {initializeApp} from 'firebase/app'
import 'firebase/auth'
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
/* 路由 */
import router from './router'
import store from './store';
/* axios */
import axios from './api'
Vue.prototype.http = axios;

Vue.use(ViewUI)
const firebaseConfig = {
  apiKey: "AIzaSyACP7t4md6Iu5IvjDqGPO1QTWmj_-OpgqY",
  authDomain: "uni-web-6c947.firebaseapp.com",
  projectId: "uni-web-6c947",
  storageBucket: "uni-web-6c947.firebasestorage.app",
  messagingSenderId: "782049310541",
  appId: "1:782049310541:web:66be912eb6db792c762904",
  measurementId: "G-0ZF19PFFM3"
};
 
initializeApp(firebaseConfig);
/* swiper */
import 'swiper/dist/css/swiper.min.css';

/* 重置样式 */
import './assets/css/reset.min.css'

/* jquery */
import 'jquery'

/* bootstarp */
import './assets/css/bootstrap.min.css'
import './assets/js/bootstrap.min'

/* animate.css */
import 'animate.css'

 
/* 头部组件 */
import Header from './components/Header'
Vue.component(Header.name,Header)


/* 尾部组件 */
import Footer from './components/Footer'
Vue.component(Footer.name,Footer)

/* 回到顶部 */
import GoTop from './components/GoTop'
Vue.component(GoTop.name,GoTop)

Vue.config.productionTip = false


router.beforeEach((to, from, next) => {
 
  if (['/baseinfo/userControl','/baseinfo/userWorks','/baseInfo/userControl','/baseInfo/userWorks','/file_chat','/video_class'].includes(to.path)) {
    if (!Cookies.get('token') || Cookies.get('username') !== "管理员") {
      if(to.meta.title){
        document.title = to.meta.title
      }
      next({ path: '/' })
    } else {
      if(to.meta.title){
        document.title = to.meta.title
      }
      next();
    }
  } else {
    if(to.meta.title){
      document.title = to.meta.title
    }
    next();
  }
    
    
});

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



