import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import config from '@/config'
const title = config.title

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    },{
      path: '/',
      redirect: '/home'
    },{
      path: '/home',
      name: 'History',
      component: resolve => require(['@/view/history'], resolve),
      meta: {
        title:'瓦克Ai'
      }
    },
   
    {
      path: '/user/privacy',
      name: 'Notice',
      component: resolve => require(['@/view/components/privacy'], resolve),
      meta: {
        title: '隐私协议',
        keepAlive: false
      }
    },
    {
      path: '/user/notice',
      name: 'Privacy',
      component: resolve => require(['@/view/components/notice'], resolve),
      meta: {
        title: '用户须知',
        keepAlive: false
      }
    },
    {
      path: '/baseInfo/userControl',
      name: 'UserControl',
      component: () => import('@/view/baseInfo/userControl'),
      meta: { title: '用户管理', auth: 'SHARE_COOPERATE_SITE' ,keepAlive: true}
    },
    {
      path: '/baseInfo/userWorks',
      name: 'UserWorks',
      component: () => import('@/view/baseInfo/userWorks'),
      meta: { title: '任务队列', auth: 'SHARE_PERSON_BIND',keepAlive: true }
    },
    
  ]
})



