<template>
  <!-- 头部整体盒子 -->
  <div id="header" class="container-fuild">
    <!-- 头部顶部 -->
    <div class="header-top container-fuild">
        <div class="pull-left">
         <span>瓦克Ai</span>你的在线内容创作顾问
        </div>
        <div class="pull-right" v-if="userInfo && userInfo.localId">
          <span>{{userInfo?userInfo.displayName:''}}</span>
          <el-popover
            placement="top-start"
            width="360"
            @show="get_voice_words"
            trigger="click">
            <div class="user-info">
              <img :src="userInfo?userInfo.photoUrl:''" alt="">
              <div>
                <div class="user-name">{{userInfo?userInfo.displayName:''}}</div>
                <div class="user-email">用户邮箱:{{userInfo?userInfo.email:''}}</div>
              </div>
            </div>
            <div class="user-credit">当前可用积分: <span>{{voice_words}}</span> ,充值积分畅享AI创作。</div>
            <div class="upgrade">
              <div class="upgrade-left">
                <div>基础会员</div>
                <div>购买积分低至10000积分/美元</div>
              </div>
              <span @click="upgrade">升级账户</span>
            </div>
            <div class="logOut" @click="logOut">退出登录</div>
            <img slot="reference" :src="userInfo?userInfo.photoUrl:''" alt="">
          </el-popover>
        </div>
    </div>
    <el-dialog title="积分购买" width="800px" :visible.sync="dialogTableVisible">
        <!-- pro -->
        <!-- <stripe-pricing-table pricing-table-id="prctbl_1QeAmrGHijBvYVwYPa5urNrB"
        publishable-key="pk_live_51QafNkGHijBvYVwYi1or8J805B1TaZQOW1jNyw6HZJrGnANDS7vYxWX0U8FQZNbwGb71foTxRQwt46Vw2SF1P5J900dNASrdLW">
        </stripe-pricing-table> -->
         <stripe-pricing-table pricing-table-id="prctbl_1Qrx1JGHijBvYVwYWYcok90T"
        publishable-key="pk_test_51QafNkGHijBvYVwYkARyJSsogyUmgPBfWUMNiuIKpwIuCEpl1XnATHYEbzruyzYazRYU6DC2PBkdollGFrxTKN5w00s5buWqK2" :client-reference-id="userInfo && userInfo.localId">
        </stripe-pricing-table>
    </el-dialog>
    <!-- 电脑导航 -->
  </div>
</template>
<script>
import config from '@/config'
import { getAuth, signOut } from "firebase/auth";
import {createPaymentIntent,get_voice_words} from '@/api/api.js';
import { loadStripe } from '@stripe/stripe-js';
export default {
  name: "Header",
  data() {
    return {
      config: config,
      baseUrl:process.env.VUE_APP_API_BASE_URL,
      userInfo:this.$store.state.userInfo,
      stripe: null,
      cardElement: null,
      voice_words:0,
      dialogTableVisible: false,
      isScriptLoaded: false,
      clientSecret: ''
    };
  },
  watch: {
   "$store.state.userInfo":{handler(val){
     console.log(val)
     this.userInfo = val
   },deep:true
   }
  },
  created(){
    
    
  },
  async mounted() {
    const script = document.createElement('script');
    script.src = 'https://js.stripe.com/v3/pricing-table.js';
    script.async = true;
    script.onload = () => {
      this.isScriptLoaded = true;
    }
    document.body.appendChild(script);
    this.stripe = await loadStripe('pk_test_51QafNkGHijBvYVwYkARyJSsogyUmgPBfWUMNiuIKpwIuCEpl1XnATHYEbzruyzYazRYU6DC2PBkdollGFrxTKN5w00s5buWqK2');
    // 标准密钥私钥  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
    // 受限的密钥私钥  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
    // const elements = this.stripe.elements();
    // this.cardElement = elements.create('card');
    // this.cardElement.mount('#card-element');
  },
  methods: {
     get_voice_words(){
        get_voice_words().then(res=>{
        if(res.code==200){
          this.voice_words = res.data.voice_words
        }
      })
      
      },
    async handlePayment() {
      this.dialogTableVisible = true;
      //支付成功回调，百度找一下
      // try {
      //   this.clientSecret = await createPaymentIntent({price:500}); // 假设金额为5000分（即50美元）
      //   console.log(this.clientSecret)
      //   const { error, paymentIntent } = await this.stripe.confirmCardPayment(
      //     this.clientSecret,
          // {
          //   payment_method: {
          //     card: this.cardElement,
          //     billing_details: {
          //       name: 'Jane Doe'
          //     }
          //   }
          // }
      //   );
      //   console.log(paymentIntent)
      //   if (error) {
      //     // 显示错误信息给用户
      //     console.error('支付失败:', error);
      //   } else if (paymentIntent.status === 'succeeded') {
      //     // 支付成功，执行后续操作
      //     console.log('支付成功:', paymentIntent);
      //   }
      // } catch (e) {
      //   console.error('支付过程中发生错误:', e);
      // }
    },
    upgrade(){
      this.handlePayment()
    },
    logOut(){
      const auth = getAuth();
      signOut(auth).then(() => {
        this.$store.commit('SET_USER',{})
        window.location.reload();
      }).catch((error) => {
        // An error happened.
      });
    }
  }
};
</script>
<style scoped>
/* 顶部 */
.pull-left {
  padding-left: 30px;
  font-size: 15px;
}
.logOut{
  cursor: pointer;
  margin: 0 10px 20px 10px;
  border: 1px solid #eee;
  border-radius: 10px;
  padding: 10px 0;
  text-align: center;
  font-size: 15px;
  position: relative;
}
.logOut > *{
 display: inline-block;
}
.logOut img{
  width: 20px;
  height: auto;
}
.logOut i{
 font-size: 19px;
}
.logOut i,.logOut img{
  position: absolute;
  left: 0;
  top: 7px;

}
.logOut:hover{
  background: #f2f4f5;
}
.pull-right{
  position: absolute;
  right: 20px; 

  font-size: 16px;
}
.pull-right img{
  display: inline-block;
  width: 45px;
  cursor: pointer;
  height: 45px;
  border-radius: 50%;
}
.user-info{
  height: 70px;
  position: relative;
  vertical-align: center;
  padding: 10px;
}
.user-credit{
  padding: 0 10px;
  color: #999;
}
.user-credit span{
  color: #2d8af5;
  font-weight: bold;
}
.user-info img{
  position: absolute;
  width: 45px;
  cursor: pointer;
  height: 45px;
  border-radius: 50%;
}
.user-info>div{
  position: absolute;
  margin: 0;
  left: 65px;
  color: #999;
}
.user-info>div .user-name{
  font-size: 16px;
  color: #000;
  font-weight: bold;
}
.upgrade{
  position: relative;
  background: linear-gradient(106.92deg, #e2ebef 14.83%, #dae8f3 31.52%, #f3f3f6 65.11%, #edd5ee 91.29%, #dfd1ec 102.83%), #bed7f4;;
  height: 100px;
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
}
.upgrade-left{
  position: absolute;
  padding:13px 10px;
  color: #999;
  line-height: 28px;
}
.upgrade-left div:nth-child(1){
  color: #000;
  font-size: 15px;
}
.upgrade>span{
  position: absolute;
  height: 30px;
  line-height: 30px;
  border-radius: 5px;
  cursor: pointer;
  color: #2d8af5;
  padding: 0 15px;
  background: #fff;
  right: 20px;
  top: 35px;
}
.pull-left span {
  font-size: 23px;
  color: #2d8af5;
  font-weight: bold;
  padding-right: 10px;
}
#header {
  background: #ffffff;
  min-width: 1080px;
  transition: all ease 0.6s;
}
#header .header-top {
  height: 60px;
  position: relative;
  /* color: #fff; */
  line-height: 60px;
  background: #ffffff;
}
/* 顶部的图标 */
#header .header-top span {
  margin: 0 8px;
}
/* 导航栏 */
#header .header-nav {
  width: 96%;
  height: 80px;
}
/* 导航栏logo */
#header .header-nav .header-nav-logo {
  height: 100%;
   width: auto;
  float: left;
  position: relative;
}
/* 导航栏logo图片 */
#header .header-nav-logo img {
  width: auto;
  height:45px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
#header .header-nav .height-60 img {
  height:60px;
}


>>> .login .el-input__inner{
  background-color: rgba(0,0,0,.08);
  border: none;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
}
>>> .login .el-input__icon{
  line-height: 50px;
  font-size: 16px;
}
.login #loginForm >*{
  margin-bottom: 20px;
}
/* 导航栏 导航容器 */
#header .header-nav-fixed .header-nav-wrapper {
  line-height: 50px;
}
#header .header-nav .header-nav-wrapper {
  line-height: 80px;
  float: right;
  margin: 0;
 
}
/* 导航栏 每个导航 */
#header .header-nav .header-nav-wrapper > li {
  float: left;
  margin: 0 15px;
  position: relative;
}
/* 导航栏 每个导航下面的 a 链接 */
#header .header-nav .header-nav-wrapper > li > a {
  color: #000;
  font-size: 15px;
  font-weight: bold;
  padding: 15px 0;
  position: relative;
}
/* 导航栏 每个导航下面的 a 链接的下划线 */
#header .header-nav .header-nav-wrapper > li > a > i {
  display: block;
  position: absolute;
  bottom: -2px;
  left: 50%;
  width: 0;
  height: 2px;
  opacity: 0;
  transition: all 0.6s ease;
  background-color: #1e73be;
}
/* 导航栏 每个导航下面的 a 链接的右侧小三角 */
#header .header-nav .header-nav-wrapper > li > a > span {
  font-size: 12px;
  transition: transform ease 0.5s;
}
/* 导航栏 每个导航下面的 a 链接 鼠标滑上去的样式 */
#header .header-nav .header-nav-wrapper > li > a:hover {
  color: #1e73be;
  text-decoration: none;
}
/* 导航栏 每个导航下面的 a 链接 鼠标滑上去下划线的样式 */
#header .header-nav .header-nav-wrapper > li > a:hover .underline {
  opacity: 1;
  width: 100%;
  left: 0;
}
/* 导航栏 每个导航下面的 a 链接 鼠标滑上去三角标的样式 */
#header .header-nav .header-nav-wrapper > li > a:hover span {
  transform: rotate(180deg);
}
/* 导航栏 每个导航下面的 a 链接 鼠标点击后的样式 */
#header .header-nav .header-nav-wrapper > li.active > a {
  color: #1e73be;
  text-decoration: none;
  border-bottom: 2px solid #1e73be;
}
/* 导航栏 每个导航下面的二级导航容器 */
#header .header-nav .header-nav-wrapper > li > dl {
  display: none;
  position: absolute;
  width: 168px;
  top: 80%;
  left: 0;
  z-index: 999999;
  box-shadow: 0 0 3px 1px #ccc;
  background: #fff;
}
/* 导航栏 每个导航下面的二级导航容器的每个导航 */
#header .header-nav .header-nav-wrapper > li > dl > dt {
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}
/* 导航栏 每个导航下面的二级导航容器的每个导航 当鼠标滑上时的样式*/
#header .header-nav .header-nav-wrapper > li > dl > dt > a:hover {
  text-decoration: none;
}
/* 导航栏 滑上一级导航显示二级导航 */
#header .header-nav .header-nav-wrapper > li:hover dl {
  display: block;
}
#header .header-nav .header-nav-wrapper > li > dl > dt:hover {
  cursor: pointer;
  background: #ccc;
}
.credit{
  color: rgb(220, 197, 23);
  font-weight: 700;
  font-size: 1.2em;
  padding: 0 6px;
}
@media screen and (max-width: 997px) {
  #header .header-nav-m {
    position: relative;
  }
  /* 导航栏logo容器 */
  #header .header-nav-m .header-nav-m-logo {
    height: 80px;
    position: relative;
  }
  /* 导航栏logo图片 */
  #header .header-nav-m .header-nav-m-logo img {
    width: 95px;
    height: 45px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
  /* 导航栏  菜单容器 */
  #header .header-nav-m .header-nav-m-menu {
    color: #fff;
    height: 50px;
    font-size: 20px;
    line-height: 50px;
    background: #474747;
    position: relative;
  }
  /* 导航栏 菜单图标 */
  #header .header-nav-m .header-nav-m-menu-wrapper {
    position: absolute;
    top: 50%;
    right: 20px;
    margin-top: -20px;
    width: 50px;
    height: 40px;
    color: #fff;
    z-index: 999999;
    font-size: 12px;
  }
  /* 导航栏 */
  #header .header-nav-m .header-nav-m-wrapper {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    background: #474747;
    z-index: 9999999;
  }
  /* 导航栏 每个导航 */
  #header .header-nav-m .header-nav-m-wrapper > li {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #ccc;
  }
  /* 导航栏 每个导航下面的 a 链接 */
  #header .header-nav-m .header-nav-m-wrapper > li > a {
    color: #fff;
    font-size: 15px;
    font-weight: bold;
    padding: 15px 0;
    position: relative;
  }
  /* 导航栏 每个导航下面的 a 链接的右侧小三角 */
  #header .header-nav .header-nav-wrapper > li > a > span {
    font-size: 10px;
  }
}
.sticky{
  position: sticky;
  top: 0;
  z-index: 999;
}
</style>



